<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0">جدول المخزون</h4>
                      <div class="page-title-right">
                          <button type="button" @click="$refs.FormModal.create()" class="btn btn-success waves-effect waves-light">
                              + إضافة مخزون جديد
                          </button>
                          <form-modal ref="FormModal" :fetchData="fetchData"/>
                      </div>

                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row">
              <div class="col-12">
                  <div class="card">
                      <div class="card-body">
                          <div class="card-title mb-5">
                            <div class="form-inline float-md-end">
                                <div class="search-box ms-2">
                                    <div class="position-relative">
                                        <input type="text" v-model="filter.search" @change="fetchData" class="form-control bg-light rounded" placeholder="Search...">
                                        <i class="mdi mdi-magnify search-icon"></i>
                                    </div>
                                </div> 
                            </div>
                          </div>
                          <div v-if="stores&&stores.data.length">
                            <div class="table-responsive" style="overflow-x: inherit;">
                                <table class="table table-nowrap table-centered mb-0">
                                    <thead>
                                        <tr>
                                            <th>أسم المخزن</th>
                                            <th>عدد الموظفين</th>
                                            <th>خيارات</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(store, index) in stores.data" >
                                            <td>{{store.name}}</td>
                                            <td>{{store.users_count}}</td>
                                            <td>
                                              <div class="btn-group">
                                                  <button type="button" class="btn btn-light dropdown-toggle" data-bs-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                                    المزيد <i class="mdi mdi-chevron-down"></i>
                                                  </button>
                                                  <div class="dropdown-menu" style="min-width: 120px;">
                                                      <router-link :to="{ name: 'store_show', params: { store_id: store.id } }" tag="a" class="dropdown-item">
                                                          <i class="far fa-eye"></i> رؤية
                                                      </router-link>
                                                      <a @click="$refs.FormModal.edit(store)" class="dropdown-item dropdownItem" href="javascript:;">
                                                        <i class="far fa-edit"></i> تعديل
                                                      </a>
                                                      <a @click="$refs.FormModal.destroy(store.id)" class="dropdown-item dropdownItem" href="javascript:;">
                                                        <i class="far fa-trash-alt"></i> حذف
                                                      </a>
                                                  </div>
                                              </div>
                                            </td>
                                        </tr>
                                        
                                    </tbody>
                                </table>
                            </div>
                            <pagination-nav class="mt-3" v-if="stores.last_page>1" :pageSize="stores.last_page" :currentPage="stores.current_page" :GoToPage="fetchData" />
                          </div>
                          <div v-else >
                            <beat-loader v-if="loader" :loading="loader" color="#5578eb"></beat-loader>
                            <p v-else class="card-title-desc" style="text-align: center;">
                              لا يوجد مخازن حتى الآن
                            </p>
                          </div>
                      </div>
                  </div>
              </div> <!-- end col -->
          </div> <!-- end row -->
      </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import axios from 'axios';
import FormModal from '../../components/modals/store/add';

export default {
  components: {LayoutDefault,PaginationNav,FormModal},
  data() {
      return {stores:null,loader:false,
      filter:{
        search:null
      }}
    },  
  created() {
    this.fetchData();
  },
  methods: {
    fetchData(num=1) {
      this.stores=null;
      this.loader=true;
      let data = {params: {page:num,filter:this.filter}};
      axios.get('/api/stores',data).then(({data}) => {
          this.stores=data.success.stores;
          if(!data.success.stores.data.length&&num!=1){
              this.fetchData(1)
          }           
          this.loader=false;
      });  
    },
  },
}
</script>