<template>
    <!--begin::Modal-->
    <div class="modal fade" id="formModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="form.id ? update() : store()" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="formModalLabel">{{form.id ?'تعديل':'إضافة'}}</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close">
                        </button>
                </div>
                <div class="modal-body row">
                    <div class="col-12 mt-3">
                        <label>أسم المخزن</label>
                        <input v-model="form.name" type="text" autocomplete="off" class="form-control" :class="{ 'is-invalid': form.errors.has('name') }">
                        <has-error :form="form" field="name"></has-error>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-light" data-bs-dismiss="modal">رجوع</button>
                    <button v-if="form.id" type="submit" class="btn btn-primary">تعديل</button>
                    <button v-else type="submit" class="btn btn-primary">حفظ</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>
<script>
import { Form, HasError } from 'vform'
import axios from 'axios';
import "vue-select/dist/vue-select.css";
export default {
    components: { HasError },
    props: ['fetchData'],
    data() {
        return {
            form: new Form({
                id: null,
                name: null,
            }),
        };
    },
    created() {
        this.getInfo();
    },
    methods: {
        create() {
            this.form.reset();
            $('#formModal').modal('show');
        },
        getInfo() {
        },
        store() {
            this.form.post("/api/stores").then(({ data }) => {
                if (data.message) {
                    this.$swal.fire({ icon: 'error', title: "Error Message", text: data.message + '!' });
                } else {
                    this.fetchData();
                    $('#formModal').modal('hide');
                }
            }).catch((error) => { console.log("Error......") })
        },
        edit(form) {
            this.form.fill(form);
            $('#formModal').modal('show');
        },
        update() {
            this.form.put("/api/stores/" + this.form.id).then((res) => {
                this.fetchData();
                $('#formModal').modal('hide');
            }).catch((error) => { console.log("Error......") })
        },
        destroy(id) {
            this.$swal.fire({
                title: 'warning',
                text: this.$t("general.r_u_s"),
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'رجوع',
                confirmButtonText: 'حذف',
            }).then((result) => {
                if (result.value) {
                    axios.delete('/api/stores/' + id).then(response => {
                        if (response.data.message) {
                            this.$swal.fire({ icon: 'error', title: 'Oops...', text: response.data.message + '!' });
                        } else {
                            this.$swal.fire('deleted', this.$t("general.delete_success"), 'success');
                            this.fetchData();
                        }
                    }).catch(() => {
                        this.$swal.fire({ icon: 'error', title: 'Oops...', text: 'Something went wrong!' });
                    });
                }
            })
        },
    },
}
</script>
